@keyframes rotate-90-cw {
	0% {
		transform: rotate(0);
	}

	100% {
		transform: rotate(45deg);
	}
}

@keyframes slide-out-right {
	0% {
		opacity: 1;
		transform: translateX(0) rotate(45deg);
	}

	100% {
		opacity: 0;
		transform: translateX(1000px) rotate(45deg);
	}
}

