@keyframes upside-rain {
  0% {
    opacity: 0;
  }
  30% {
    opacity: 0;
  }
  32% {
    opacity: .075;
  }
  50% {
    opacity: .075;
  }
  52% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

@keyframes upside-down {
  0% {
    filter: none;
  }
  30% {
    filter: none;
  }
  31% {
    filter: brightness(400%);
  }
  32% {
    filter: brightness(400%);
  }
  33% {
    filter: sepia(100%) hue-rotate(180deg) saturate(450%) contrast(115%) brightness(75%);
  }
  50% {
    filter: sepia(100%) hue-rotate(180deg) saturate(450%) contrast(115%) brightness(75%);
  }
  51% {
    filter: brightness(400%);
  }
  52% {
    filter: brightness(400%);
  }
  53% {
    filter: none;
  }
  100% {
    filter: none;
  }
}

@keyframes el-shake {
  0% {
    transform: translate(0, 0);
  }
  15% {
    transform: translate(1px, 0);
  }
  30% {
    transform: translate(0, 1px);
  }
  45% {
    transform: translate(-1px, 1px);
  }
  60% {
    transform: translate(-1px, 0);
  }
  75% {
    transform: translate(0, 0);
  }
  85% {
    transform: translate(0, -1px);
  }
  100% {
    transform: translate(0, 0);
  }
}

@keyframes blink {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 0;
  }
  21% {
    opacity: .8;
  }
  23% {
    opacity: 0;
  }
  60% {
    opacity: 0;
  }
  61% {
    opacity: .95;
  }
  63% {
    opacity: 0;
  }
  70% {
