html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  height: 100%;
}

body {
  margin: 0;
}

.container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-right: 0.75em;
  padding-left: 0.75em;
}

.container::after {
  clear: both;
  content: "";
  display: block;
}

.page-section,
.small-section {
  padding-right: 0;
  padding-left: 0;
  background-attachment: scroll;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  box-sizing: border-box;
  display: block;
  overflow: hidden;
  position: relative;
  width: 100%;
}

@media only screen and (min-width: 1024px) {
  .page-section,
  .small-section {
    background-attachment: fixed;
  }
}

.page-section {
  padding-top: 60px;
  padding-bottom: 60px;
}

@media only screen and (min-width: 480px) {
  .page-section {
    padding-top: 80px;
    padding-bottom: 80px;
  }
}

@media only screen and (min-width: 768px) {
  .page-section {
    padding-top: 120px;
    padding-bottom: 120px;
  }
}

@media only screen and (min-width: 1024px) {
  .page-section {
    padding-top: 140px;
    padding-bottom: 140px;
  }
}

.small-section {
  padding-top: 30px;
  padding-bottom: 30px;
}

@media only screen and (min-width: 480px) {
  .small-section {
    padding-top: 50px;
    padding-bottom: 50px;
  }
}

@media only screen and (min-width: 768px) {
  .small-section {
    padding-top: 70px;
    padding-bottom: 70px;
  }
}

