#text_inputs {
  background: rgba(57, 63, 84, 0.8);
  border-radius: 2px;
  display: flex;
  flex-direction: row;
  margin: 0 auto;
  max-width: 400px;
  padding: 16px 24px;
  position: relative;
  width: 100%;
}

#text_inputs::after {
  animation: gradient 3s linear infinite;
  background: linear-gradient(to right, #b294ff, #57e6e6, #feffb8, #57e6e6, #b294ff, #57e6e6);
  background-position: 0% 0%;
  background-size: 500% auto;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
  bottom: 0;
  content: "";
  height: 2px;
  left: 0;
  position: absolute;
  right: 0;
  z-index: 999;
}

#text_inputs.is-focus button > svg {
  transform: rotate(45deg);
}

#text_inputs input {
  background: transparent;
  border-style: none;
  color: #bfd2ff;
  flex-grow: 1;
  font-size: 18px;
  line-height: 24px;
  outline: none;
  vertical-align: middle;
}

#text_inputs input::-webkit-input-placeholder {
  color: #7881a1;
}

#text_inputs button {
  -webkit-appearance: button;
  background: none;
  border: 0;
  height: 29px;
  line-height: 1;
  margin: 0;
  outline: none;
  padding: 0;
  transition: color .25s;
  vertical-align: middle;
}

#text_inputs button.send-it > svg {
  fill: #bfd2ff;
  animation: slide-out-right 0.5s cubic-bezier(0.55, 0.085, 0.68, 0.53) both;
}

#text_inputs button > svg {
  animation: flicker-in-2 2s linear both;
  fill: #7881a1;
  height: 29px;
  transform: rotate(0deg);
  transition: transform 0.75s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

#text_inputs button > svg:hover {
  fill: #bfd2ff;
}

