.st {
  color: #1c0502;
  font-family: ITC Benguiat W01;
  font-size: 8rem;
  font-weight: 800;
  letter-spacing: -2px;
  margin: auto;
  text-align: center;
  text-shadow: -0.05rem -0.05rem 1px #ed2b12, 0.05rem -0.05rem 1px #ed2b12, -0.05rem 0.05rem 1px #ed2b12, 0.05rem 0.05rem 1px #ed2b12, 0 0 15px #630100, 0 0 20px #450100;
  text-transform: uppercase;
  width: 625px;
}

.st-bottom {
  clear: both;
  left: .6rem;
  letter-spacing: 2px;
  position: relative;
  top: -3.75rem;
}

.st-drop {
  line-height: 0;
  position: relative;
  top: 1.55rem;
}

.st p {
  display: inline-block;
  margin: 0;
}

.st p span {
  animation: flicker 4s linear 1s;
}

.st-bound {
  animation: flicker 4s linear 1s;
  box-shadow: -0.05rem -0.05rem 2px #ed2b12, 0.05rem -0.05rem 2px #ed2b12, -0.05rem 0.05rem 2px #ed2b12, 0.05rem 0.05rem 2px #ed2b12;
  height: 5px;
}

.st-bound-full {
  margin-right: auto;
  margin-bottom: -1.2rem;
  margin-left: 5px;
  max-width: 612px;
  width: 100%;
}

.st-bound-mini {
  margin-top: .5rem;
}

.st-bound-left {
  float: left;
  left: 5px;
  position: relative;
  width: 99px;
}

.st-bound-right {
  float: right;
  left: .3rem;
  position: relative;
  width: 103px;
}

