.mobile .home-section {
  background-attachment: scroll;
}

.home-section {
  background-attachment: fixed;
  background-image: url("../img/et.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
  overflow: hidden;
  position: relative;
  width: 100%;
}

@media only screen and (max-width: 1024px) {
  .home-section {
    background-attachment: scroll;
  }
}

.home-content {
  display: table;
  height: 100%;
  position: relative;
  text-align: center;
  width: 100%;
}

.home-text {
  display: table-cell;
  height: 100%;
  vertical-align: middle;
}

.fixed-height-small {
  box-sizing: border-box;
  min-height: 600px;
}

@media only screen and (max-width: 480px) {
  .fixed-height-small {
    min-height: 300px;
  }
}

.the-upside-down {
  background-image: linear-gradient(to top, #082c45 35%, #0d7b96 42.5%, #1e93b3 50%, #24d9f9 100%);
  overflow: hidden;
  position: relative;
}

.the-upside-down canvas {
  max-height: 100%;
  max-width: 100%;
}

.the-upside-down svg {
  bottom: 0;
  left: 0;
  margin: auto;
  position: absolute;
  right: 0;
  top: 0;
}

.the-upside-down svg path {
  animation: dash 5s linear forwards;
  fill: none;
  stroke: #ff0000;
  stroke-dasharray: 1000;
  stroke-dashoffset: 1000;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-miterlimit: 10;
  stroke-width: 2;
}

@keyframes dash {
  to {
    stroke-dashoffset: 0;
  }
}
